

body{
  background: rgb(192, 186, 186);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.maincontainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
 
}
.framecontainer{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frame{
  
  

  
 height: 400px;
 width:700px;
  border: 5px solid black;
}

.colorwrapper{
  
  display: flex;
  flex-direction: column;
  align-items: center;
}

.circle{
  cursor: pointer;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  box-shadow:3px 7px grey
  
  

}

.circle:hover{
  height: 45px;
  width: 45px;
}

.tools {
  display: flex;
  flex-direction: column;
  
}

#spacing{
  height: 50px;
  width: 50px;
}
#button{
  width:85px;
  text-align: center;
  cursor: pointer;
  margin:5px;
  border-radius: 20px;
  background: transparent;
  border: 2px solid black;
  padding:5px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-overflow: clip;
}

#button:hover{
  background: white;
}